'use client'

import { axiosInstance } from '@/shared/axiosInstance'
import React, { useEffect, useState } from 'react'

export default function Page(): React.JSX.Element {
    const [departments, setDepartments] = useState<Department[]>([])

    type Department = {
        id: string
        parentId: string
        name: string
        identifier: string
        path: string
        depth: number
        isActive: boolean
        createdAt: Date
        updatedAt: Date
        positionCount: number
    }

    useEffect(() => {
        const fetchDepartments = async () => {
            const data = await axiosInstance.get<Department[]>('http://localhost:5129/top-positions').then(res => res.data).catch(() => [])
            setDepartments(data)
        }
        
        fetchDepartments()
    }, [])

  return (
    <div>
        <h1>Departments</h1>
        <ul>
            {departments.map((department) => (
                <li key={department.id}>{department.name}</li>
            ))}
        </ul>
    </div>
  )
}
